@import "variables.scss";

.start {
  width: 90%;
  padding: 0 5%;
  display: grid;
  place-items: center;
  grid-template-columns: repeat(2, 1fr);

  &__header {
  }

  &__yellow {
    color: $mainColor;
    font-size: $h1FontSize;
  }

  &__desc {
    color: darkgray;
    text-align: justify;
  }
}

@media (orientation: portrait) {
  .start {
    grid-template-columns: repeat(1, 1fr);
    row-gap: 2rem;

    text-align: center;
  }
}
@media screen and (max-width: 960px) {
  .start {
    grid-template-columns: repeat(1, 1fr);
    row-gap: 2rem;
    text-align: center;
  }
}
