@import url("https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,400;0,700;1,400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Goldman&display=swap");

$mainColor: rgb(255, 217, 1);
$mainHoverColor: rgb(253, 235, 129);

$h1FontSize: calc(32px + 1vw);
$h2FontSize: calc(24px + 0.9vw);
$h3FontSize: calc(18px + 0.7vw);
$h4FontSize: calc(16px + 0.4vw);
$h5FontSize: calc(13px + 0.3vw);
$h6FontSize: calc(10px + 0.3vw);

$normalFontSize: calc(16px + 0.1vw);
