@import "variables.scss";

.navbar {
  height: 110px;
  width: 100%;
  display: grid;
  place-items: center;
  position: relative;

  &__backBtn {
    position: absolute;
    left: 5%;
    color: $mainColor;
    background: none;
    border: none;
    outline: none;
    font-size: $h1FontSize;

    &:hover {
      cursor: pointer;
      color: $mainHoverColor;
    }
  }

  &__logo {
    font-family: "Goldman", cursive;
    font-size: $h2FontSize;
    text-decoration: none;
    color: white;

    span {
      color: $mainColor;
      font-family: "Goldman", cursive;
      font-size: $h2FontSize;
    }

    &:hover {
      cursor: pointer;
    }
  }

  &__form {
    width: 100%;
    display: grid;
    place-items: center;
  }

  &__input {
    width: 50%;
    background: inherit;
    border: none;
    outline: none;
    color: white;
    border-bottom: 1px solid darkgray;
    padding: 0.5rem 1rem;

    &::placeholder {
      color: darkgray;
    }

    &:focus {
      border-bottom: 1px solid white;
    }
  }

  &__submit {
    display: none;
  }
}

@media (orientation: portrait) {
  .navbar {
    &__input {
      width: 65%;
    }
  }
}
