@import "variables.scss";

.movieCard {
  background: inherit;
  //width: fit-content;
  display: grid;
  place-items: center;
  grid-row-gap: 1rem;
  min-height: 200px;
  margin: 1rem;
  position: relative;

  &__img {
    width: 100%;
    height: auto;
  }

  &__info {
    position: absolute;
    bottom: 0;
    background: rgba(0, 0, 0, 0.4);

    backdrop-filter: blur(15px);
    width: 101%;
    min-height: 20%;
    display: grid;
    place-items: center;
    grid-row-gap: 0.25rem;
    padding: 0.75rem 0;
  }

  &__title {
    width: 95%;
    font-family: "Goldman", cursive;
    text-align: center;
  }

  &__btn {
    padding: 0.5rem 1rem;
    border: none;
    outline: none;
    border-radius: 0.3rem;
    background: $mainColor;

    &:hover {
      cursor: pointer;
      background: $mainHoverColor;
    }
  }
}
