*{margin:0;padding:0;font-family:"Karla", sans-serif;font-size:calc(16px + 0.1vw)}h1{font-size:calc(32px + 1vw)}h2{font-size:calc(24px + 0.9vw)}h3{font-size:calc(18px + 0.7vw)}h4{font-size:calc(16px + 0.4vw)}h5{font-size:calc(13px + 0.3vw)}h6{font-size:calc(10px + 0.3vw)}body{background:#060022;color:whitesmoke}

.navbar{height:110px;width:100%;display:grid;place-items:center;position:relative}.navbar__backBtn{position:absolute;left:5%;color:#ffd901;background:none;border:none;outline:none;font-size:calc(32px + 1vw)}.navbar__backBtn:hover{cursor:pointer;color:#fdeb81}.navbar__logo{font-family:"Goldman", cursive;font-size:calc(24px + 0.9vw);text-decoration:none;color:white}.navbar__logo span{color:#ffd901;font-family:"Goldman", cursive;font-size:calc(24px + 0.9vw)}.navbar__logo:hover{cursor:pointer}.navbar__form{width:100%;display:grid;place-items:center}.navbar__input{width:50%;background:inherit;border:none;outline:none;color:white;border-bottom:1px solid darkgray;padding:0.5rem 1rem}.navbar__input::-webkit-input-placeholder{color:darkgray}.navbar__input:-ms-input-placeholder{color:darkgray}.navbar__input::-ms-input-placeholder{color:darkgray}.navbar__input::placeholder{color:darkgray}.navbar__input:focus{border-bottom:1px solid white}.navbar__submit{display:none}@media (orientation: portrait){.navbar__input{width:65%}}

.start{width:90%;padding:0 5%;display:grid;place-items:center;grid-template-columns:repeat(2, 1fr)}.start__yellow{color:#ffd901;font-size:calc(32px + 1vw)}.start__desc{color:darkgray;text-align:justify}@media (orientation: portrait){.start{grid-template-columns:repeat(1, 1fr);grid-row-gap:2rem;row-gap:2rem;text-align:center}}@media screen and (max-width: 960px){.start{grid-template-columns:repeat(1, 1fr);grid-row-gap:2rem;row-gap:2rem;text-align:center}}

.searchedMovies{width:90%;padding:0 5%;margin-top:1rem;display:grid}.searchedMovies--noEmpty{grid-template-columns:repeat(auto-fill, minmax(16rem, 1fr))}.searchedMovies--empty{place-items:center}.searchedMovies__empty{text-align:center}

.movieCard{background:inherit;display:grid;place-items:center;grid-row-gap:1rem;min-height:200px;margin:1rem;position:relative}.movieCard__img{width:100%;height:auto}.movieCard__info{position:absolute;bottom:0;background:rgba(0,0,0,0.4);-webkit-backdrop-filter:blur(15px);backdrop-filter:blur(15px);width:101%;min-height:20%;display:grid;place-items:center;grid-row-gap:0.25rem;padding:0.75rem 0}.movieCard__title{width:95%;font-family:"Goldman", cursive;text-align:center}.movieCard__btn{padding:0.5rem 1rem;border:none;outline:none;border-radius:0.3rem;background:#ffd901}.movieCard__btn:hover{cursor:pointer;background:#fdeb81}

.movieInfo{width:calc(80% - 6rem);margin:0 10%;margin-top:4rem;border:0.5rem solid white;display:flex;flex-direction:row;align-items:center;background-color:#1b1b1b;padding:2rem}.movieInfo__poster{width:15vw;height:100%}.movieInfo__title{text-transform:uppercase}.movieInfo__infos{padding:2rem;display:grid;grid-row-gap:0.75rem;row-gap:0.75rem}.movieInfo__info{text-transform:uppercase;color:#ffd901}.movieInfo__infoSpan{font-weight:normal;text-transform:none;color:white}.movieInfo__plot{font-style:italic}@media (orientation: portrait){.movieInfo{width:calc(80% - 2rem);flex-direction:column;padding:1rem 0rem}.movieInfo__poster{width:30vw;height:100%}.movieInfo__title{text-align:center}}@media screen and (max-width: 960px){.movieInfo{width:calc(80% - 2rem);flex-direction:column;padding:1rem 0rem}.movieInfo__poster{width:30vw;height:100%}.movieInfo__title{text-align:center}}

