@import "variables.scss";

.movieInfo {
  width: calc(80% - 6rem);
  margin: 0 10%;
  margin-top: 4rem;
  border: 0.5rem solid white;
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: rgb(27, 27, 27);
  padding: 2rem;

  &__poster {
    width: 15vw;
    height: 100%;
  }

  &__title {
    text-transform: uppercase;
  }

  &__infos {
    padding: 2rem;
    display: grid;
    row-gap: 0.75rem;
  }

  &__info {
    text-transform: uppercase;
    color: $mainColor;
  }

  &__infoSpan {
    font-weight: normal;
    text-transform: none;
    color: white;
  }

  &__plot {
    font-style: italic;
  }
}

@media (orientation: portrait) {
  .movieInfo {
    width: calc(80% - 2rem);
    flex-direction: column;
    padding: 1rem 0rem;

    &__poster {
      width: 30vw;
      height: 100%;
    }

    &__title {
      text-align: center;
    }
  }
}

@media screen and (max-width: 960px) {
  .movieInfo {
    width: calc(80% - 2rem);
    flex-direction: column;
    padding: 1rem 0rem;

    &__poster {
      width: 30vw;
      height: 100%;
    }

    &__title {
      text-align: center;
    }
  }
}
